Vue.prototype.wxShare = function (title, desc, link, imgUrl) {

  //const url = location.href;
  const url = window.location.href.split('#')[0];
  if(!imgUrl) {
    imgUrl = 'http://img.jiarixiaodui.com/haoshi-logo.jpg';
  }
  axios.get('https://api.jiarixiaodui.com/v1/wx/config', {
    params: {
      url: url
    }
  }).then(response => {// 获得签名配置
    var Data = response.data;
    // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，
    wx.config({
      debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
      appId: Data.appId,   // 必填，公众号的唯一标识
      timestamp: Data.timestamp, // 必填，生成签名的时间戳
      nonceStr: Data.nonceStr,   // 必填，生成签名的随机串
      signature: Data.signature, // 必填，签名，见附录1
      jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
    });
  });

  wx.ready(() => {
    // 所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，
    // 则可以直接调用，不需要放在ready函数中。
    wx.onMenuShareAppMessage({ // 分享给朋友
      title: title, // 分享标题
      desc: desc,   // 分享描述
      link: link,   // 分享链接 默认以当前链接
      imgUrl: imgUrl,// 分享图标

      // // 用户确认分享后执行的回调函数
      // success: function () {
      //   if(!window.localStorage.getItem('token')) return;
      //   var params = new URLSearchParams();
      //   params.append('token', window.localStorage.getItem('token'));
      //   params.append('type', 'share');
      //   http.post(shareCallback(), params).then(res => {
      //     if (res.data.error == 0) { // 表示当天分享成功
      //       store.commit('shareChange', {
      //         isShare: true
      //       });

      //     } else {
      //       return;
      //     }
      //   })
      // },
      // // 用户取消分享后执行的回调函数
      // cancel: function () {
      //   console.log('分享到朋友取消');
      // }
    });
    //分享到朋友圈
    wx.onMenuShareTimeline({
      title: title, // 分享标题
      desc: desc,
      link: link,
      imgUrl: imgUrl,
      // 用户确认分享后执行的回调函数
      // success: function () {
      //   if(!window.localStorage.getItem('token')) return;
      //   var params = new URLSearchParams();
      //   params.append('token', window.localStorage.getItem('token'));
      //   params.append('type', 'share');
      //   http.post(shareCallback(), params).then(res => {
      //     if (res.data.error == 0) { //表示当天分享成功
      //       store.commit('shareChange', {
      //         isShare: true
      //       });
      //     } else {
      //       return;
      //     }
      //   });
      // },
      // // 用户取消分享后执行的回调函数
      // cancel: function () {
      //   console.log('分享到朋友圈取消');
      // }

    });
  });
};

Vue.config.productionTip = false

new Vue({
   store,
  router,
  axios,
  render: h => h(App)
}).$mount('#app')
