import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)


  const routes =  [
    { name:"/",path: '/',component: resolve => require(['@/components/home'], resolve),meta:{ pageTitle: '首页', keepAlive: true }  },
   
    { name:"/JiaMeng/index",path: '/JiaMeng/index',component: resolve => require(['@/components/JiaMeng/index'], resolve),meta:{ pageTitle: '加盟服务'      }  },
  
    { name:"/ShouYe/productDetail",path: '/ShouYe/productDetail',component: resolve => require(['@/components/ShouYe/productDetail'], resolve),meta:{ pageTitle: '产品详情'}  },
  
  ]


const router = new VueRouter({
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }

  })


router.beforeEach((to, from, next) => {
  //directRightUrl();
  //设置页面标题
  typeof to.meta.pageTitle !== undefined && setDocumentTitle(to.meta.pageTitle);
  //判断是否登录跳转

    next();

})

/**
 * 兼容微信设置页面的title
 */
let setDocumentTitle = function (title) {
    document.title = title;
    let ua = navigator.userAgent;
    if (/\bMicroMessenger\/([\d\.]+)/.test(ua) && /ip(hone|od|ad)/i.test(ua)) {
        var i = document.createElement('iframe');
        i.src = '/favicon.ico';
        i.style.display = 'none';
        i.onload = function () {
            setTimeout(function () {
                i.remove();
            }, 9);
        };
        document.body.appendChild(i);
    }
};


/**
 * 如果url里没有?,在url里的#前添加？再跳转
 */
let directRightUrl = function () {
  let paths = window.location.href.split('#')
  paths[1] = paths[1] || '/'
  // 老式的#!分隔跳转
  if (paths[0].charAt(paths[0].length - 1) !== '?') {
    paths[0] = `${paths[0]}?`
  }
  if (paths[1].charAt(0) === '!') {
    paths[1] = paths[1].substr(1)
  }
  let url = `${paths[0]}#${paths[1]}`

  if (window.location.href !== url) {
    window.location.href = url
  }
};

export default router;
